import React, {useContext} from "react";
import "./Podcast.scss";
import {podcastSection} from "../../portfolio";
import {Fade} from "react-reveal";
import StyleContext from "../../contexts/StyleContext";

export default function Podcast() {
  const {isDark} = useContext(StyleContext);

  if (!podcastSection)
    console.error("podcastSection object for Podcast section is missing");

  if (!podcastSection.display) {
    return null;
  }
  return (
    <Fade bottom duration={1000} distance="20px">
      <div className="main" id="podcast">
        <div className="podcast-header">
          <h1 className="podcast-header-title">{podcastSection.title}</h1>
          <p
            className={
              isDark
                ? "dark-mode podcast-header-subtitle"
                : "subTitle podcast-header-subtitle"
            }
          >
            {podcastSection.subtitle}
          </p>
        </div>
        <div className="podcast-main-div">
          {podcastSection.podcast.map((podcastLink, i) => {
            if (!podcastLink) {
              console.log(
                `Podcast link for ${podcastSection.title} is missing`
              );
            }
            return (
              <a className="podcast" href={podcastLink} key={i}>
                <img
                  src={
                    "https://s3.castbox.fm/1a/8e/bf/63179a41febf64e2314d40de61.jpg"
                  }
                  alt="podcast"
                />
                {i === 0 ? (
                  <p>Episode {Number(i) + 1}: Dives deep on phobia.</p>
                ) : (
                  <p>
                    Episode {Number(i) + 1}: Dives much more deeeply on phobia.
                  </p>
                )}
              </a>
            );
          })}
        </div>
      </div>
    </Fade>
  );
}
